@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: 'Lato', sans-serif;
}

.about, .papers, .resume {
	background-color: #f3f8f8;
	height: 200vh;
	margin-left: 15em;
	color: #424242;
	transition: 500ms;
	
}

.paper{
	line-height: 2;
	font-family: "Times New Roman";
	padding:10px;
	color:black;
}

.home{
	margin-left: 15em;
	height: 100%;
	background-image: url(/guy_react/static/media/CERN_particles.f87e988e.png);
	background-repeat: no-repeat;
	background-color: black;
	color: white;
	background-size: 100% auto;
	text-align: center;
	transition: 500ms;

}

.textbox{
  background-color: lightgrey;
  min-width: 100%;
  min-height: 100vh;
  background: rgba(0, 0, 90, 0.7);
  margin: auto;
  padding:30px;
}

hr {clear:both;}

.block{
  width: 400px;
  padding: 20px;
  height: auto;
  float: left;
}

#guy{
	float:right;
	width: 400px;
	padding: 20px;
}

#guy_portrait{
  width: 300px;
  margin:auto;
}

@media screen and (max-width: 600px) {
  #guy_portrait, #guy, .block{display:none;}
}

@media screen and (max-width: 960px) {
  .home,.about,.papers,.resume{
	  margin-left: 0em;
	  transition: 0ms;
  }
}


p,h1,h4{
	line-height: 2;
	font-family: "Times New Roman";
	padding:10px;
}
p{
	padding: 15px;
	font-size: 100%;
}
h1{
	text-align: center;
	font-size: 200%;
}
h4{
	font-size: 120%;
}

.navbar{
	background-color: #060b26;
	height: 60px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
a,.contact-info, .contact-me{
	text-decoration:none;
	font-size:130%;
	padding-right:20px;
	color: white;
}

.menu-bars{
	margin-left: 2rem;
	font-size: 2rem;
	background: none;
	visibility: hidden;
}

.nav-menu{
	background-color: #060b26;
	width: 250px;
	height: 100vh;
	display: flex;
	justify-content: center;
	position: fixed;
	top: 0;
	left: -100%;
	transition: 500ms;
}

.nav-menu.active{
	left: 0;
	transition: 350ms;
}

.nav-text{
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 8px 0px 8px 16px;
	list-style: none;
	height: 60px;
}

.nav-text a{
	text-decoration: none;
	color: #f5f5f5;
	font-size: 18px;
	width: 95%;
	height: 100%;
	display: flex;
	align-items: center;
	padding: 0 16px;
	border-radius: 4px;
}
	
.nav-text a:hover{
	background-color: #1a83ff;
}

.nav-menu-items{
	width: 100%;
}

.navbar-toggle{
	background-color: #060b26;
	width: 100%;
	height: 80px;
	display: flex;
	justify-content: flex-start;
	align-items:center;
	text-decoration: none;
}
.contact{
	padding-top:190px;
}
.contact-me, .contact-info{
	width: 100%;
	height: 25px;
	padding-left:40px;
	display: flex;
	align-items:center;
	text-decoration: none;
	justify-content: flex-start;
}

@media screen and (max-width: 960px) {
	.menu-bars{
		visibility:visible;
	}
	.nav-menu{
		left:0;
	}
	.nav-menu.active{
		left:-100%;
	}
}

@media screen and (min-width: 960px) {
	.menu-bars{
		visibility:hidden;
	}
	.nav-menu{
		left:0;
	}
	.nav-menu.active{
		left:0%;
	}
}


.contact-info{
	font-size: 13px;
}

span{
	margin-left: 16px;
}

